
#app {
    padding-left: 25%;
    padding-right: 25%;
}
.row {
    clear: both;
}

.header-logo {
    float: left;
}
.header-logo img {
    width: 100px;
}
.header-menu {
    float: right;
}
.header-menu li {
    list-style-type: none;
    display: inline;
}
.header-menu li::after {
    content: '|';
    padding-left: 10px;
    padding-right: 3px;
    color: #cacaca;
}
.header-menu li:last-child::after {
    content: '';
}
a:link, a:hover, a:visited {
    text-decoration: none;
    color: black;
}





























.join-form {
    text-align: center;
}
.join-form input {
    border-style: groove;
    height: 30px;
    width: 400px;
    border-radius: 5px;
    outline-color: #41cda3;
    padding-left: 10px;
}
.join-form button {
    height: 35px;
    width: 80px;
    border: none;
    border-radius: 5px;
}
.join-form button:first-child {
    background: #33c197;
    color: white;
}

